@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100;400;700&display=swap);
:root {
  --shadow: 0 0 36px rgb(0 0 0 /0.1);
  --dark-gray: #222222;
  --gray: rgb(99, 99, 99);
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 16px;
  font-family: "Noto Sans TC", sans-serif;
}
body {
  outline: black solid 1px;
}
a {
  word-break: break-all;
  color: #333;
}

div > img {
  width: 100%;
}
h1 {
  font-size: 32px;
}
h2 {
  font-size: 20px;
}

.App {
  max-width: 480px;
  margin: 0 auto;
}

.icon-button {
  height: 48px;
  width: 48px;
  border: none;
  border-radius: 100%;
}
.icon-button.solid {
  background-color: rgb(255 255 255 / 1);
  color: grey;
  box-shadow: 0 0 36px rgb(0 0 0 /0.1);
  box-shadow: var(--shadow);
}
.icon-button.white {
  background-color: rgb(255 255 255 / 0.5);
  color: #fff;
}
.icon-button.dark {
  background-color: #222222;
  background-color: var(--dark-gray);
  color: #fff;
}
input {
  padding: 8px 12px;
  border-radius: 1000px;
  border: 1px solid transparent;
  outline: none;
  font-size: 14px;
  box-shadow: inset 0 0 16px rgb(0 0 0 / 0.08);
}
.explore-page {
  padding: 16px;
  position: relative;
}
.explore-page .filter-button {
  position: absolute;
  top: 16px;
  right: 16px;
}
.explore-page form {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 16px;
  gap: 16px;
  padding: 16px;
  margin: 32px 0;
}
.explore-page label[for="search"] {
  width: 100%;
  /* margin-bottom: 16px; */
  font-size: 32px;
}
.explore-page input {
  flex: 1 1;
}

.spot {
  width: 100%;
  height: 0;
  padding-bottom: 56.24%;
  margin-bottom: 16px;
  position: relative;
  overflow: hidden;
  border-radius: 32px;
  box-shadow: 0 0 36px rgb(0 0 0 /0.1);
  box-shadow: var(--shadow);
}
.spot::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(rgb(0 0 0 / 0), rgb(0 0 0 / 0.3));
}
.spot-img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.spot-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 50% 50%;
}
.spot-brief {
  position: absolute;
  bottom: 24px;
  left: 16px;
  z-index: 1;
  color: #fff;
}
.spot-bried h2 {
  letter-spacing: 1px;
}
.spot-rating {
  display: inline-block;
  padding: 4px 8px;
  margin-bottom: 8px;
  border-radius: 100px;
  background: rgb(255 255 255 /0.3);
  color: #fff;
  font-size: 14px;
}
.spot-rating > svg {
  width: 14px;
  height: 14px;
  margin-right: 4px;
  fill: rgb(255, 255, 220);
}
.cities {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 8px;
  gap: 8px;
}
.city {
  width: 120px;
  height: 80px;
  position: relative;
  border-radius: 16px;
  background-color: rgb(99, 99, 99);
  background-color: var(--gray);
  overflow: hidden;
}
.city::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(rgb(0 0 0 / 0), rgb(0 0 0 / 0.3));
}
.city h4 {
  color: #fff;
  position: absolute;
  bottom: 8px;
  left: 8px;
  z-index: 1;
}
.city-page header {
  height: 30vh;

  background: url("https://images.unsplash.com/photo-1505996873815-567390ee9e20?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1770&q=80")
    center / cover;
}

.label {
  display: inline-block;
  padding: 4px 8px;
  border-radius: 8px;
  background-color: rgb(99, 99, 99);
  background-color: var(--gray);
  font-size: 14px;
  color: #fff;
}
.spot-page {
  padding: 16px;
}
.spot-page header {
  width: 100%;
  height: 50vh;
  padding: 16px;
  position: relative;
  border-radius: 32px;
  overflow: hidden;
  background: grey;
}
.spot-page .back-button {
  position: relative;
  z-index: 2;
}
.spot-page .spot-header-img {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.spot-header-img img {
  height: 100%;
  object-position: cover;
  object-position: center;
}

.spot-page header::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 0;
  background: linear-gradient(
    rgb(0 0 0 / 0.5) 0%,
    rgb(0 0 0 / 0) 25%,
    rgb(0 0 0 / 0) 75%,
    rgb(0 0 0 / 0.5) 100%
  );
}

.spot-page .title-content {
  color: #fff;
  position: absolute;
  bottom: 24px;
  z-index: 3;
}

.spot-page h1 {
  margin-bottom: 16px;
}
.spot-page h2 {
  font-size: 20px;
  margin-bottom: 16px;
}
.spot-page .spot-info {
  padding: 16px;
  padding-left: 40px; /* 24+16 */
  margin: 8px 0;
  border-radius: 32px;
  color: grey;
  font-size: 14px;
  text-indent: -24px;
  box-shadow: 0 0 36px rgb(0 0 0 /0.1);
  box-shadow: var(--shadow);
}
.spot-page .spot-info * svg {
  height: 16px;
  width: 16px;
  vertical-align: middle;
  margin: 4px;
}
.spot-desc {
  padding: 16px;
  box-shadow: 0 0 36px rgb(0 0 0 /0.1);
  box-shadow: var(--shadow);
  border-radius: 32px;
  color: rgb(53, 53, 53);
}
.spot-update-time {
  margin: 16px;
  font-size: 12px;
  color: lightgrey;
}
.spot-update-time svg {
  height: 16px;
  width: 16px;
  margin-right: 4px;
}
.spot-contact {
  display: flex;
  grid-gap: 16px;
  gap: 16px;
  margin-top: 24px;
  position: -webkit-sticky;
  position: sticky;
  bottom: 16px;
  z-index: 3;
}
.spot-contact::after {
  content: "";
  position: absolute;
  top: -16px;
  bottom: -16px;
  left: 0;
  right: 0;
  background: linear-gradient(rgb(255 255 255 / 0), rgb(255 255 255 / 1));
}
.spot-contact a {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1 1;
  padding: 12px 8px;
  z-index: 4;
  border: none;
  border-radius: 16px;
  background-color: #222222;
  background-color: var(--dark-gray);
  color: #fff;

  text-decoration: none;
}
a.text-button > svg {
  height: 20px;
  width: 20px;
  margin-right: 8px;
}

